import { createSlice } from '@reduxjs/toolkit';
import { loginSchool } from '@/redux/Actions/School/SchoolActions';

const schoolSlice = createSlice({
  name: 'school',
  initialState: {
    schoolInfo: null,
    loading: false,
    logined:false,
    error: null,
  },
  reducers: {
    logoutSchool: (state) => {
      if (typeof window !== 'undefined') {
        localStorage.removeItem('token');
        localStorage.removeItem('schoolInfo');
      }
      state.schoolInfo = null;
      state.loading = false;
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(loginSchool.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(loginSchool.fulfilled, (state, action) => {
        state.loading = false;
        state.logined=true;
        state.schoolInfo = action.payload;
      })
      .addCase(loginSchool.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export const { logoutSchool } = schoolSlice.actions;
export default schoolSlice.reducer;
